$darkGrey: rgba(22, 24, 35, 0.5);
$textDark: rgba(22, 24, 35, 0.34);
$dark: rgba(22, 24, 35, 0.03);
$dark1: rgba(22, 24, 35, 0.06);
$dark2: rgba(22, 24, 35, 0.12);
$dark3: rgba(255, 255, 255, 0);
$dark4: rgb(0, 0, 0);
$dark5: rgba(22, 24, 35, 0.12);
$dark_white: rgba(22, 24, 35, 0.03);
$grey: rgb(138, 150, 163);
$red: rgb(254, 44, 85);

.wrapper {
  padding-top: 60px;
  .title {
    font-weight: 600;
    font-size: 5rem;
    line-height: 1;
    margin-top: 60px;
  }
  .subTitle {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 24px;
    margin-top: 2px;
    color: $darkGrey;
    display: block;
  }
  .content {
    display: flex;
    margin-top: 24px;
  }
  .upload {
    height: 100%;
    cursor: pointer;
  }
  .image {
    width: 49px;
    height: 36px;
  }
  .card {
    width: 306px;
    height: 544px;
    background: $dark;
    border-radius: 8px;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    transition: 0.15s ease-in-out 0s;
    &:hover {
      background-color: $dark1;
    }
  }
  .textMain {
    font-weight: 400;
    font-size: 2rem;
    line-height: 24px;
    text-align: center;
    color: $textDark;
    margin-top: 29px;
  }
  .textSub {
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 18px;
    color: $textDark;
    text-align: left;
    margin: 0;
    padding-left: 45px;
  }
  .textSubMargin {
    margin: 16px 0;
  }
  .textSubMenu {
    list-style: unset;
  }
  .uploadBtn {
    height: 0px;
    width: 0px;
    opacity: 0;
    position: absolute;
  }
  .form {
    margin-left: 60px;
    margin-bottom: 111px;
    flex: 1 1 0%;
    .captionWrap {
      margin-bottom: 45px;
    }
    .textContainer {
      display: flex;
      -webkit-box-pack: justify;
      justify-content: space-between;
      -webkit-box-align: center;
      align-items: center;
    }
    .font {
      font-size: 24px;
      font-weight: 400;
      line-height: 1;
    }
    .requireFont {
      font-weight: 400;
      font-size: 13px;
      line-height: 17px;
      color: $darkGrey;
      white-space: pre;
    }
    .gapTop {
      margin-top: 14px;
      .containerInside {
        min-height: 44px;
        position: relative;
        text-align: center;
        border: 1px solid $dark2;
        border-radius: 2px;
      }
      .editor {
        font-weight: 400;
        font-size: 1.5rem;
        line-height: 18px;
        cursor: text;
        width: 100%;
        position: relative;
        padding: 0px 80px 0px 16px;
        &:focus-within {
          box-shadow: rgb(34 90 89 / 20%) 2px 4px 20px;
          border-radius: 4px;
          border-style: none;
          outline-style: none;
        }
      }
      .draftEditorRoot {
        position: relative;
      }
      .draftEditorContainer {
        background-color: $dark3;
        border-left: 0.1px solid transparent;
        position: relative;
        z-index: 1;
      }
      .publicDraftEditorContent {
        padding: 12px 0px;
        overflow: auto;
        border-style: none;
        outline: none;
        user-select: text;
        white-space: pre-wrap;
        overflow-wrap: break-word;
        text-align: left;
      }
      .iconStyle {
        position: absolute;
        width: 20px;
        height: 20px;
        margin-left: 8px;
        top: 12px;
        cursor: pointer;
        font-size: 1.6rem;
        &.hash {
          right: 12px;
        }
        &.at {
          right: 40px;
        }
      }
    }
  }
  .title {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    vertical-align: top;
    color: $dark4;
    margin-bottom: 12px;
  }
  .containerIn {
    position: relative;
    line-height: 0;
    padding: 9px;
    &.empty {
      border: 1px solid $dark5;
      border-radius: 2px;
    }
    .bgContainer {
      display: block;
    }
    .candidate {
      width: 66px;
      height: 118px;
      object-fit: cover;
    }
    .candidate.empty {
      background-color: $dark_white;
    }
  }
  .formItem {
    margin-top: 45px;
    display: flex;
    .permission {
      &:first-child {
        margin-right: 55px;
      }
      .titledesc {
        font-weight: 400;
        font-size: 24px;
        line-height: 32px;
        vertical-align: top;
        margin-bottom: 12px;
        color: $dark2;
      }
      .radioGroup {
        margin-bottom: 26px;
        display: flex;
      }
      .radio {
        margin-right: 32px;
        display: inline-block;
      }
      .inputCheck {
        display: none;
      }
      .radioBox {
        flex: 0 0 22px;
        display: inline-block;
        width: 22px;
        height: 22px;
        margin-right: 13px;
        vertical-align: middle;
        border-radius: 11px;
        position: relative;
        cursor: pointer;
        background: $dark_white;
        border: 1.5px solid $dark2;
      }
      .radioBox.checked {
        border-color: $grey;
        background: $dark_white;
        border: 1.5px solid $dark3;
      }
      .radioBox.checked::before {
        content: '';
        display: block;
        width: 14px;
        height: 14px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -7px;
        margin-top: -7px;
        background: $red;
        border-radius: 7px;
      }
    }
  }
  .checkboxContainer {
    display: flex;
    .checkbox {
      margin-right: 32px;
      display: inline-block;
    }
    .checkbox .radioBox.checked::before {
      content: '';
      display: block;
      width: 14px;
      height: 14px;
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -7px;
      margin-top: -7px;
      background: $red;
      border-radius: unset;
    }
    .radioCheck.radioBox {
      border-radius: unset;
    }
  }
  .part {
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
  }
}
